import * as React from 'react';
import { navigate } from 'gatsby';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Container from '@mui/material/Container';
import IconButton from '@mui/material/IconButton';
import Link from '@mui/material/Link';
import List from '@mui/material/List';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import SwipeableDrawer from '@mui/material/SwipeableDrawer';
import Typography from '@mui/material/Typography';
import LaunchIcon from '@mui/icons-material/Launch';
import MenuIcon from '@mui/icons-material/Menu';
import { scrollToTop, scrollToSection } from '../utils/scroll';
import { menuHeight } from './layout';
import Logo from '../images/logo.svg';
import { PHONE } from '../../const';
import { Underline } from './underline';

export const Menu = ({
  height,
  lists = []
}: {
  height: string;
  lists: any[];
}) => {
  const [isOpen, setIsOpen] = React.useState(false);

  const toggleDrawer = (_isOpen: boolean) => (event: any) => {
    if (
      event &&
      event.type === 'keydown' &&
      (event.key === 'Tab' || event.key === 'Shift')
    ) {
      return;
    }

    setIsOpen(_isOpen);
  };

  return (
    <AppBar
      position='fixed'
      sx={{
        bgcolor: 'background.default'
        // boxShadow: 'none'
      }}
    >
      <Container>
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
            height
          }}
        >
          <Box>
            <Link
              href='#top'
              underline='none'
              sx={{
                display: 'block',
                fontSize: 24
              }}
              onClick={e => {
                e.preventDefault();
                scrollToTop();
              }}
            >
              <img src={Logo} alt='VALEK' width={120} />
            </Link>
          </Box>
          <Box display='flex' alignItems='center'>
            {lists.map(
              ({ label, link, isCurrent, isButton, isExternal }, i) => {
                const isAnchor = /^#.+$/.test(link);

                const Component = (props: any) =>
                  isButton ? (
                    <Button
                      {...props}
                      variant='contained'
                      disableElevation={true}
                      sx={{ ...props.sx, borderRadius: '0px' }}
                    >
                      {props.children}
                    </Button>
                  ) : (
                    <Link underline='hover' {...props}>
                      <Typography color='text.primary' component='span'>
                        {props.children}
                      </Typography>
                    </Link>
                  );

                return (
                  <Component
                    key={i}
                    href={link}
                    target={isExternal && '_blank'}
                    sx={{
                      margin: '0 1em',
                      display: {
                        md: 'inline',
                        xs: 'none'
                      },
                      pointerEvents: isCurrent ? 'none' : '',
                      opacity: isCurrent ? 0.5 : 1
                    }}
                    onClick={(e: any) => {
                      if (!isExternal) {
                        //  href 
                        e.preventDefault();
                      }

                      if (isAnchor) {
                        if (link === '#top') {
                          scrollToTop();
                        } else {
                          // 
                          scrollToSection(
                            link.replace(/^#(.*)$/, '$1'),
                            menuHeight * -1
                          );
                        }
                      } else {
                        //  navigate 
                        navigate(link);
                      }
                    }}
                  >
                    {label}
                  </Component>
                );
              }
            )}
            <Link
              color='text.primary'
              href={`tel:${PHONE}`}
              sx={{
                fontSize: 20,
                fontWeight: 'bold',
                textDecoration: 'none'
              }}
            >
              <Underline>{PHONE}</Underline>
            </Link>
            {lists.length > 0 && (
              <IconButton
                onClick={toggleDrawer(true)}
                sx={{
                  display: {
                    md: 'none',
                    xs: ''
                  }
                }}
              >
                <MenuIcon />
              </IconButton>
            )}
          </Box>
        </Box>
      </Container>

      <SwipeableDrawer
        anchor='right'
        open={isOpen}
        onClose={toggleDrawer(false)}
        onOpen={toggleDrawer(true)}
      >
        <Box
          // @TODO
          sx={{
            backgroundColor: 'background.default',
            color: 'primary.main',
            width: 200,
            height: '100%'
          }}
          role='presentation'
          onClick={toggleDrawer(false)}
          onKeyDown={toggleDrawer(false)}
        >
          <List>
            {lists.map(({ label, link, isExternal }, i) => {
              const isAnchor = /^#.+$/.test(link);

              return (
                <ListItemButton
                  key={i}
                  href={link}
                  target={isExternal && '_blank'}
                  onClick={e => {
                    if (!isExternal) {
                      //  href 
                      e.preventDefault();
                    }

                    if (isAnchor) {
                      // 
                      scrollToSection(
                        link.replace(/^#(.*)$/, '$1'),
                        menuHeight * -1
                      );
                    } else {
                      //  navigate 
                      navigate(link);
                    }
                  }}
                >
                  <ListItemText
                    primary={
                      <Typography color='text.primary'>
                        {label}
                        {isExternal && (
                          <LaunchIcon sx={{ ml: 0.5, width: 10, height: 10 }} />
                        )}
                      </Typography>
                    }
                  />
                </ListItemButton>
              );
            })}
          </List>
        </Box>
      </SwipeableDrawer>
    </AppBar>
  );
};
